import React, { Component, Fragment } from 'react';
import Header from '../header/Header.js';
import Footer from '../footer/Footer.js';
import Product from './Product';
import Filters from './Filters';
import celulares from '../../json/celulares';
import NotFound from '../assets/NotFound';
import InformationProduct from './InformationProduct';

class App extends Component{

  state = {
    allProducts: celulares,
    textNotFound: "",
    productsAdded: false,
    showModal: false,
    position: undefined,
    image: undefined,
    number: 0,
    price: 0,
    titleProduct: undefined
  }

  /* Filtro de búsqueda */

  search = x =>{
    this.setState({
      allProducts: x
    })
  }

  /* Filtro de orden */

  orderProducts = x =>{
    this.setState({
      allProducts: x
    })
  }

  /* Filtro de casillas de verificación */

  onFilterChange = x =>{
    this.setState({
      allProducts: x
    })
  }

  /* Filtro por precios */

  onFilterPrizes = x =>{
    this.setState({
      allProducts: x
    })
  }

  /* Cambiar mensaje de No se ha encontrado */

  changeTextNotFound = text => {
    this.setState({
      textNotFound: text
    })
  }

  /* Mostrar el número de productos agregados */

  numberProductsAdded = () => {
    this.setState({
      productsAdded: true
    })
  }

  /* Mostrar información de productos */
  showModal = condition =>{
    this.setState({
      showModal: condition
    })
  }

  /* Information Products */

  setPosition = number =>{
    this.setState({
      position: number
    })
  }

  setInformation = (image, number, price, titleProduct) => {
    this.setState({
      image: image,
      number: this.state.number + Number(number),
      price: Number(price) * Number(number),
      titleProduct: titleProduct
    })
  }

  hideInformation = () =>{
    if (document.getElementsByClassName('product-added').length === 0){
      this.setState({
        number: 0
      })
    }
  }

  render(){
    return(
      <Fragment>
        <Header
        onSearch={this.search}
        changeTextNotFound={this.changeTextNotFound}
        productsAdded={this.state.productsAdded}
        image={this.state.image}
        number={this.state.number}
        price={this.state.price}
        titleProduct={this.state.titleProduct}
        hideInformation={this.hideInformation}
        />
        <div className="container">
          <aside className="edit-products">
            <Filters
            changeElements={this.onFilterChange}
            changeOrder={this.orderProducts}
            onSearch={this.search}
            searchByPrizes={this.onFilterPrizes}
            changeTextNotFound={this.changeTextNotFound}
            products={this.state.allProducts} />
          </aside>
          <section className="products">
          
          {this.state.allProducts.length > 0 ? (this.state.allProducts.map( (x, index) => (
              <Product
              increase={this.increase}
              showModal={this.showModal}
              numberProducts={this.numberProductsAdded}
              key={index}
              id={x.id}
              imagen={x.imagen}
              modelo={x.modelo}
              stock={x.stock}
              precio={x.precio}
              position={this.setPosition}
              setInformation={this.setInformation}
              />
              )
            ))
            : <NotFound text={this.state.textNotFound} />
          }
          </section>
        </div>
        {this.state.showModal === true ? <InformationProduct i={this.state.position} showModal={this.showModal} /> : null}
        <Footer />
      </Fragment>
    )
  }
}

export default App;